.home {
    position: relative;
    height: 100vh;
    color: white;
}

.home .container-fluid {
    height: 100%;
}

.home .intro {
    position: absolute;
    /* background-color: aliceblue; */
    top: 30%;
    left: 10%;
    font-family: Kefa;
}

.home .greetings {
    font-size: 4rem;
}

.home .name {
    font-size: 4rem;
}

.home .name span {
    color: yellowgreen;
    font-family: monospace;
    font-weight: 900;
}

.card {
    padding: 10%;
    width: 100%;
}

.home .image {
    background-image: url('../../asserts/about_bg.jpeg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    background-size: cover;
    height: 100%;
}

.home .Typewriter {
    font-size: 2rem;
}

.home .typewriter {
    margin-top: 14px;
}